'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { supabase } from '@/lib/supabase';

export default function CreateStorePage() {
  const router = useRouter();
  const [form, setForm] = useState({
    name: '',
    city: '',
    type: 'hub',
    alias: '',
    password: '',
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = async () => {
    const { error } = await supabase.from('store_admin').insert([form]);
    if (!error) router.push('/admin/dashboard/manage-store');
  };

  return (
    <div className="max-w-md mx-auto space-y-4">
      <h2 className="text-xl font-semibold mb-4">Create Store</h2>

      <Input placeholder="Store Name" name="name" value={form.name} onChange={handleChange} />
      <Input placeholder="City" name="city" value={form.city} onChange={handleChange} />
      <select name="type" value={form.type} onChange={handleChange} className="w-full border p-2 rounded">
        <option value="hub">Hub</option>
        <option value="garage">Garage</option>
      </select>
      <Input placeholder="Alias (unique)" name="alias" value={form.alias} onChange={handleChange} />
      <Input placeholder="Password" name="password" type="password" value={form.password} onChange={handleChange} />

      <Button onClick={handleSubmit} className="w-full">Create</Button>
    </div>
  );
}
